.btnArrow {
  position: relative;
  border-radius: 5px;
  font-family: 'DIN Pro Cond', sans-serif;
  font-size: 18px;
  line-height: 1;
  color: #fff;
  width: 251px;
  height: 76px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding-right: 60px;
  border: 0;
  transition: all 0.1s ease-in;
  overflow: hidden;
}
.btnArrow:before,
.btnArrow:after {
  content: '';
  position: absolute;
  z-index: -1;
  transition: all 0.1s ease-in;
}
.btnArrow:before {
  top: 0;
  bottom: 0;
  right: 60px;
  left: 0;
  background: #ea7722;
  border: 1px solid #ea7722;
  border-radius: 5px 0 0 5px;
}
.btnArrow:after {
  content: '';
  position: absolute;
  right: 0px;
  top: 0px;
  width: 60px;
  bottom: 0px;
  background: linear-gradient(to right, #d36b1f, #e67521);
  border-radius: 0 5px 5px 0;
  z-index: -1;
  transition: all 0.1s ease-in;
}
.btnArrow span {
  max-height: 56px;
  overflow: hidden;
}
.btnArrow span:after {
  content: '';
  position: absolute;
  right: 0px;
  top: 0px;
  width: 60px;
  bottom: 0px;
  background: #fff;
  border: 1px solid #ea7722;
  border-left: 0;
  border-radius: 0 5px 5px 0;
  z-index: -1;
  transition: all 0.1s ease-in;
}
.btnArrow svg {
  position: absolute;
  right: 22px;
  top: 50%;
  transform: translateY(-50%);
  fill: #fff;
  width: 17px;
  height: 16px;
  transition: all 0.1s ease-in;
}
.btnArrow input {
  position: absolute;
  right: 0;
  bottom: 0;
  width: 200%;
  height: 100%;
  opacity: 0;
  cursor: pointer;
}
.btnArrow:hover {
  color: #262626;
}
.btnArrow:hover:before {
  background-color: #fff;
}
.btnArrow:hover:after {
  opacity: 0;
}
.btnArrow:hover svg {
  fill: #d36b1f;
}
.btnArrow:active {
  color: #fff;
  box-shadow: inset 0 10px 18px 0 rgba(56,56,56,0.23);
  border-color: #c56a27;
}
.btnArrow:active:before {
  background-color: #ea7722;
}
.btnArrow:active:after {
  opacity: 1;
}
.btnArrow:active svg {
  fill: #fff;
}
.btnArrow--line {
  height: 61px;
  width: auto;
  padding: 0 93px 0 26px;
}
.btnArrow--block {
  width: 100%;
}
.btnArrow--outline {
  color: #222;
}
.btnArrow--outline:before {
  background: #fff;
}
.btnArrow--outline:after {
  opacity: 0;
}
.btnArrow--outline svg {
  fill: #d36b1f;
}
