.preloader {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: #ec3434;
  z-index: 100;
  animation: 1s linear 2s 1 normal forwards preloader;
}
.preloader__container {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 60%;
  min-width: 300px;
  opacity: 0;
  height: 300px;
  animation: 0.5s ease-in-out 0.5s 1 normal forwards preloaderContainer;
}
.preloader__container svg {
  display: block;
  width: 100%;
  height: 300px;
  left: 0;
  top: 0;
  fill: transparent;
  stroke: #fff;
  stroke-width: 0.5;
}
.preloader__overlay {
  height: 0;
  overflow: hidden;
  animation: 1s ease-in-out 0.8s 1 normal forwards preloaderOverlay;
}
.preloader__overlay svg {
  fill: #fff;
}
@-moz-keyframes preloader {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@-webkit-keyframes preloader {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@-o-keyframes preloader {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@keyframes preloader {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
@-moz-keyframes preloaderContainer {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
